/**
 * components/global.css
 *
 * Project-wide styles
 *
 */

div.seomatic-field {
  border: 1px solid #e3e5e8;
  margin-bottom: 10px;
  padding: 0;
  border-radius: 3px;
  background-color: rgb(235, 237, 239);
}

div.seomatic-tab-content {
  flex: 1;
  background: #fff;
  padding: 24px;
  word-wrap: break-word;
}

nav.seomatic-tabs {
  position: relative;
  z-index: 1;
  box-shadow: inset 0 -1px 0 #e3e5e8;
  min-height: 40px;
  overflow: hidden;
}

nav.seomatic-tabs ul {
  display: flex;
  flex-direction: row;
  width: calc(100% + 1px);
}

nav.seomatic-tabs ul li {
  box-sizing: border-box;
  border-right: 1px solid rgba(0, 0, 20, 0.1);
}

nav.seomatic-tabs ul li a {
  position: relative;
  display: block;
  padding: 10px 24px;
  white-space: nowrap;
  overflow: hidden;
  color: #576575;
  text-decoration: none;
}

nav.seomatic-tabs ul li a:active, nav.seomatic-tabs ul li a:focus {
  outline: 0;
  border: none;
  -moz-outline-style: none;
}

nav.seomatic-tabs ul li a.sel {
  color: #29323d;
  background: #fff;
  padding-bottom: 10px;
  cursor: default;
}

nav.seomatic-tabs ul li a:not(.sel):hover {
  color: #0d78f2;
}

nav.seomatic-tabs ul li a:after {
  background: linear-gradient(to right, rgba(235, 237, 239, 0), #ebedef 17px);
}

nav.seomatic-tabs ul li a.sel:after {
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #fff 17px);
}

div.tokenfield {
  border: 1px solid #e3e5e8;
  border-radius: 3px;
  padding: 5px 7px;
  box-sizing: border-box;
}

div.inline-item-padding {
  padding-left: 10px;
}


span.inline-item,
div.inline-item {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
}

.inline-item > div,
.inline-item > div > label {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
}

div.half-width-field {
  display: inline-block;
  width: 45%;
}

div.half-width-field-left {
  padding-right: 5%;
}

p.instructions {
  margin-top: 4px;
  font-size: 11px;
  line-height: 1.4;
  color: #8f98a3;
}

div.preview-column {
  float: none;
  width: 100%;
}

div.displaypreview {
  width: 512px;
  overflow: hidden;
  border: 1px solid #EEE;
  padding: 30px;
}

ul.checkboxChecklist {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li.checkboxChecklist {
  position: relative;
  list-style-type: none!important;
  color: #333;
}

li.checkboxChecklist.checked {
  color: #008002;
}

li.checkboxChecklist label {
  position: absolute;
  width: 14px;
  height: 14px;
  top: 3px;
  left: -22px;
  background: transparent;
  border:1px solid;
}

li.checkboxChecklist a {
  color: inherit;
}

li.checkboxChecklist.checked label:after {
  content: '';
  position: absolute;
  width: 9px;
  height: 5px;
  background: transparent;
  top: 2px;
  left: 2px;
  border: 2px solid;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
}

